import React from 'react'
import Badge from '@/src/components/base/Badge'
import Button from '@/src/components/base/Button'
import { Clock } from 'lucide-react'

type CourseProps = {
  title: string
  description: string
  duration: string
  monthlyFee: string
  image: string
}

const Course = ({
  title,
  description,
  duration,
  monthlyFee,
  image,
}: CourseProps) => {
  return (
    <div className="group rounded-2xl shadow-md hover:shadow-xl transition-all duration-300 overflow-hidden hover:-translate-y-1">
      <div className="relative h-44">
        <div
          className="absolute inset-0 bg-cover bg-center"
          style={{ backgroundImage: `url(${image})` }}
        />
        <div className="absolute inset-0 bg-linear-to-t from-black/60 via-black/20 to-transparent" />

        <Badge
          variant="secondary"
          className="absolute top-4 left-4 text-xs flex items-center gap-1"
        >
          <Clock size={12} />
          {duration} months
        </Badge>
      </div>

      <div className="flex flex-col gap-3 p-4">
        <h2 className="text-lg font-semibold text-black group-hover:text-primary transition">
          {title}
        </h2>

        <p className="text-sm text-black/70 line-clamp-2">
          {description}
        </p>

        <div className="flex items-center justify-between pt-4 mt-2 border-t border-t-black/10">
          <span className="text-lg font-bold text-black">
            LKR {monthlyFee}
            <span className="text-sm font-medium text-black/60 ml-1">
              / month
            </span>
          </span>

          <Button variant="primary" size="sm" className="rounded-full">
            Enroll Now
          </Button>
        </div>
      </div>
    </div>
  )
}

export default Course
